body {
  background-color: #f5f5f5;
  justify-content: center;
  align-items: center;
  height: 100vh;
  margin: 0;
  font-family: Arial, sans-serif;
  display: flex;
}

.form-container {
  background-color: #fff;
  border-radius: 5px;
  padding: 20px;
  box-shadow: 0 2px 4px #0000001a;
}

.form-label {
  margin-bottom: 5px;
  display: block;
}

.form-input, .form-textarea {
  border: 1px solid #ccc;
  border-radius: 4px;
  width: 100%;
  margin-bottom: 10px;
  padding: 8px;
}

.form-button {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 4px;
  padding: 10px 15px;
}

.form-button:hover {
  background-color: #218838;
}

.success-message {
  color: green;
  margin-top: 10px;
}
